}

:root {
  --color-red-0: #fff6f6;
  --color-red-100: #ffdada;
  --color-red-200: #ff888b;
  --color-red-300: #da2530;
  --color-red-400: #93000f;
  --color-red-500: #410003;
  --color-blue-0: #f5f8ff;
  --color-blue-100: #d5e3ff;
  --color-blue-200: #71adff;
  --color-blue-300: #2d73bc;
  --color-blue-400: #004880;
  --color-blue-500: #001c37;
  --color-green-0: #e3ffe8;
  --color-green-100: #64ff91;
  --color-green-200: #00c559;
  --color-green-300: #248142;
  --color-green-400: #005322;
  --color-green-500: #002109;
  --color-yellow-0: #fff9d4;
  --color-yellow-100: #f5e550;
  --color-yellow-200: #bbae00;
  --color-yellow-300: #7b7324;
  --color-yellow-400: #434800;
  --color-yellow-500: #1f1c00;
  --color-gray-0: #f8f8f9;
  --color-gray-100: #e2e2e7;
  --color-gray-200: #aaaab3;
  --color-gray-300: #707079;
  --color-gray-400: #464652;
  --color-gray-500: #1b1b1e;
  --color-shade-0: rgba(0, 0, 0, 0.03);
  --color-shade-100: rgba(0, 0, 0, 0.1);
  --color-shade-200: rgba(0, 0, 0, 0.3);
  --color-shade-300: rgba(0, 0, 0, 0.534);
  --color-shade-400: rgba(0, 0, 0, 0.7);
  --color-shade-500: rgba(0, 0, 0, 0.9);
}

html {
  scroll-padding-top: calc(var(--site-header-height) * 1.25);
}

body {
  background: #f8f8f9;
}

:root {
  --site-header-height: 120px;
}
@media (max-width: 1000px) {
  :root {
    --site-header-height: 80px;
  }
}

.site-header {
  display: flex;
  align-items: center;
  background-color: #f8f8f9;
  border-bottom: 1px solid #e2e2e7;
  height: var(--site-header-height);
  width: 100%;
  padding: 0 28px;
  padding: 0 max(50% - 1280px / 2, 28px);
  position: sticky;
  top: 0;
  z-index: 3;
}

.site-branding {
  display: flex;
  align-items: center;
  gap: 2rem;
  color: #464652;
  height: 100%;
  text-decoration: none;
  white-space: nowrap;
}
.site-branding__logo {
  max-height: 60%;
}

@media (min-width: 1000px) {
  .main-nav {
    margin-left: auto;
  }
}
@media (max-width: 1000px) {
  .main-nav {
    background: #fff;
    border-left: 1px solid #e2e2e7;
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 50vw;
    padding: 16px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    transform: translateX(100%);
    z-index: 2;
  }
  .main-nav:not([hidden]) {
    transform: translateX(0);
  }
}
@media (max-width: 414px) {
  .main-nav {
    width: 100%;
  }
}
@media (min-width: 1000px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 1000px) {
  .main-nav__toggle {
    margin-left: auto;
  }
}
@media (min-width: 1000px) {
  .main-nav__close {
    display: none;
  }
}
.main-nav__list {
  display: flex;
}
@media (max-width: 1000px) {
  .main-nav__list {
    flex-direction: column;
  }
}
@media (min-width: 1000px) {
  .main-nav__item {
    --link-padding: 0 16px;
  }
  .main-nav__item:last-child {
    --link-padding: 0 0 0 16px;
  }
}
.main-nav__link {
  color: #707079;
  display: block;
  height: 56px;
  line-height: 56px;
  padding: 0 16px;
  padding: var(--link-padding, 0);
  text-decoration: none;
  transition: color 0.15s ease-in;
  white-space: nowrap;
}
.main-nav__link[aria-current] {
  color: #1b1b1e;
  font-weight: 600;
}
@media (any-hover: hover) {
  .main-nav__link:hover {
    color: #1b1b1e;
  }
}
@media (max-width: 1000px) {
  .main-nav__link {
    border-bottom: 1px solid #e2e2e7;
  }
}

.page {
  background: #fff;
  min-height: calc(100vh - var(--site-header-height));
}
@media (min-width: 1000px) {
  .page {
    padding-top: calc(var(--site-header-height) * 0.7);
  }
}

.page-content {
  display: grid;
  align-items: start;
}
@media (max-width: 1000px) {
  .page-content {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-template-areas: "side-nav" "main";
  }
}
@media (min-width: 1000px) {
  .page-content {
    grid-template-columns: 1fr 3fr;
    grid-template-areas: "side-nav main";
  }
}

.page-category {
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  margin: 0 0 8px;
  text-transform: uppercase;
}
@media (max-width: 1000px) {
  .page-category {
    display: none;
  }
}

.side-nav {
  grid-area: side-nav;
}
@media (max-width: 1000px) {
  .side-nav {
    display: flex;
    align-items: start;
    flex-direction: column;
    border-bottom: 1px solid #e2e2e7;
    z-index: 2;
  }
}
@media (min-width: 1000px) {
  .side-nav {
    max-height: calc(100vh - var(--site-header-height) * 1.75);
    overflow-y: auto;
    position: sticky;
    top: calc(var(--site-header-height) * 1.25);
  }
}
.side-nav__teaser {
  display: flex;
  align-items: center;
  gap: 16px;
  height: 56px;
  width: 100%;
  position: relative;
}
.side-nav__teaser svg {
  width: 16px;
}
@media (min-width: 1000px) {
  .side-nav__teaser {
    display: none;
  }
}
.side-nav__toggle {
  appearance: none;
  background: transparent;
  border: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  text-align: right;
}
.side-nav__toggle svg {
  width: 16px;
}
.side-nav__list {
  color: #464652;
  font-size: 0.875rem;
}
@media (max-width: 1000px) {
  .side-nav__list {
    padding-bottom: 16px;
    width: 100%;
  }
}
.side-nav__link {
  display: flex;
  align-items: center;
  color: inherit;
  height: 48px;
  text-decoration: none;
}
@media (pointer: fine) {
  .side-nav__link {
    padding: 0 16px;
  }
}
@media (any-hover: hover) {
  .side-nav__link {
    transition: background 0.15s ease-in, color 0.15s ease-in;
  }
  .side-nav__link:hover {
    background: #f8f8f9;
    color: #1b1b1e;
  }
}
.side-nav__link[aria-current] {
  color: #1b1b1e;
  font-weight: 700;
}
.side-nav .TOC__list {
  counter-reset: toc-heading;
  color: #707079;
  font-size: 0.75rem;
  margin-left: 24px;
}
.side-nav__item > .TOC__list {
  margin-left: 32px;
}

.side-nav .TOC__item {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 0 0.5ch;
}
.side-nav .TOC__item::before {
  content: counters(toc-heading, ".") ".";
  counter-increment: toc-heading;
}
.side-nav .TOC__item .TOC__list {
  grid-column: 1/-1;
}
.side-nav .TOC__link {
  display: flex;
  align-items: center;
  color: inherit;
  height: 40px;
  text-decoration: none;
}
@media (any-hover: hover) {
  .side-nav .TOC__link:hover {
    color: #1b1b1e;
  }
}
@media (pointer: fine) {
  .side-nav .TOC__link {
    height: 32px;
  }
}
.side-nav .TOC__link[aria-current] {
  color: #1b1b1e;
  font-weight: bold;
}

main {
  grid-area: main;
  overflow-x: hidden;
  padding-bottom: 24px;
}
@media (min-width: 1000px) {
  main {
    padding-left: 32px;
  }
}
@media (max-width: 1000px) {
  main {
    padding-top: 24px;
  }
}

.lead {
  color: #707079;
  font-size: 1.25rem;
  line-height: 1.75;
  margin-bottom: 32px;
}

:is(h2, h3, h4, h5, h6)[data-anchored] {
  --heading-anchor-color: transparent;
  display: grid;
  grid-template-columns: 1.5ch auto;
  position: relative;
  left: -1.5ch;
}
:is(h2, h3, h4, h5, h6)[data-anchored]:hover {
  --heading-anchor-color: #707079;
}
:is(h2, h3, h4, h5, h6)[data-anchored] .heading-anchor {
  color: var(--heading-anchor-color);
  text-decoration: none;
  transform: scale(0.7);
  transition: color 0.15s ease-in;
}
:is(h2, h3, h4, h5, h6)[data-anchored] .heading-anchor:hover, :is(h2, h3, h4, h5, h6)[data-anchored] .heading-anchor:focus {
  --heading-anchor-color: #1b1b1e;
}

.comparison-box {
  display: flex;
  gap: 24px;
  margin-bottom: 24px;
}
@media (max-width: 768px) {
  .comparison-box {
    flex-direction: column;
  }
}
.comparison-box__side {
  flex: 1;
}
.comparison-box__example {
  background: var(--comparison-box-example-background-color);
  padding: 24px;
  color: var(--comparison-box-example-foreground-color);
}
.comparison-box__example:not(:last-child) {
  margin-bottom: 24px;
}
@media (max-width: 768px) {
  .comparison-box__example {
    border-left: 6px solid var(--comparison-box-example-highlight-color);
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
  }
}
@media (min-width: 768px) {
  .comparison-box__example {
    border-top: 6px solid var(--comparison-box-example-highlight-color);
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
  }
}
.comparison-box__example--positive {
  --comparison-box-example-highlight-color: #00c559;
  --comparison-box-example-background-color: #f8f8f9;
  --comparison-box-example-heading-color: #005322;
  --comparison-box-example-foreground-color: #002109;
}
.comparison-box__example--negative {
  --comparison-box-example-highlight-color: #ff888b;
  --comparison-box-example-background-color: #fff6f6;
  --comparison-box-example-heading-color: #93000f;
  --comparison-box-example-foreground-color: #410003;
}
.comparison-box__header {
  font-weight: 700;
  display: flex;
  gap: 16px;
  align-items: center;
  color: var(--comparison-box-example-heading-color);
  margin-bottom: 16px;
}
.comparison-box__header .Icon {
  color: var(--comparison-box-example-heading-color);
  height: 24px;
  width: 24px;
}

.example-box {
  border: 1px solid #e2e2e7;
  margin-bottom: 24px;
}
:root .example-box pre[class*=language-] {
  margin: 0;
}
.example-box__result {
  background: #f8f8f9;
  border-bottom: 1px solid #e2e2e7;
  padding: 24px;
}
.example-box__tabs {
  padding: 16px;
}

.fake-item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f8ff;
  border-radius: 3px;
  height: 100%;
  line-height: 0;
  padding: 32px;
  text-align: center;
}

.api-table td:nth-of-type(2),
.api-table td:nth-of-type(3) {
  white-space: nowrap;
}

table:not([class]),
.table {
  line-height: 1.5;
  width: 100%;
  margin: 16px 0 32px;
}
table:not([class]) th, table:not([class]) td,
.table th,
.table td {
  border-bottom: 1px solid #e2e2e7;
  border-top: 1px solid #e2e2e7;
  text-align: left;
}
table:not([class]) th,
.table th {
  background: #f8f8f9;
  font-weight: 600;
  padding: 16px;
  white-space: nowrap;
}
table:not([class]) th[colspan],
.table th[colspan] {
  text-align: center;
}
table:not([class]) td,
.table td {
  transition: background 0.8s cubic-bezier(0, 1, 0.4, 1), box-shadow 0.8s cubic-bezier(0, 1, 0.4, 1);
  padding: 8px 16px;
}
table:not([class]) td ul,
.table td ul {
  margin: 0;
  padding: 0;
}
table:not([class]) tr:hover td,
.table tr:hover td {
  background: #f5f8ff;
}

